import React from "react";
import FsLightbox from "fslightbox-react";
import "assets/css/black-dashboard-react.scoped.css";
import "assets/scss/black-dashboard-react.scoped.scss";
import "./messagestyles.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faDownload,
  faMagnifyingGlassPlus,
} from "@fortawesome/free-solid-svg-icons";

class MediaMessage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      toggler: false,
      mediaTypes: {
        image: ["jpg", "jpeg", "png", "jpe", "gif"],
        video: ["mp4"],
        audio: ["mpeg", "ogg"],
        browserDocument: ["pdf"],
      },
    };
  }

  youtubeId = (url) => {
    let id = undefined;
    if (url?.search(/youtu.be/i) !== -1) {
      id = url?.split("/").at(-1);
    } else if (url?.search(/youtube.com/i) !== -1) {
      id = url?.split("=").at(-1);
    }
    return id;
  };

  render() {
    const mediaType = this.props?.mediaUrl?.split(".").at(-1);
    const isNotImageOrVideo =
      !this.state.mediaTypes.image.includes(mediaType) &&
      !this.state.mediaTypes.video.includes(mediaType) &&
      !this.state.mediaTypes.audio.includes(mediaType);

    return (
      <>
        <div className="fullscreen-image">
          <div className="d-flex flex-column">
            <button
              className="source"
              style={{
                background: "transparent",
                border: "0",
                width: "100%",
                textAlign: this.props.senderType === "User" ? "left" : "right",
              }}
              onClick={() => this.setState({ toggler: !this.state.toggler })}
            >
              {/* Images */}
              {this.state.mediaTypes.image.includes(mediaType) && (
                <img
                  src={this.props?.mediaUrl}
                  alt="message-media"
                  style={{
                    objectFit: "contain",
                    maxHeight: "450px",
                  }}
                />
              )}

              {/* Video */}
              {this.state.mediaTypes.video.includes(mediaType) && (
                <video
                  controls
                  autoPlay={false}
                  name="media"
                  width="300rem"
                  height="220rem"
                >
                  <source
                    src={this.props.mediaUrl}
                    type={"video/" + mediaType}
                  />
                  Your browser does not support the video tag.
                </video>
              )}

              {/* Audio */}
              {this.state.mediaTypes.audio.includes(mediaType) && (
                <div className="mw-100">
                  <audio controls autoPlay={false}>
                    <source
                      src={this.props.mediaUrl}
                      type={"audio/" + mediaType}
                    />
                    Your browser does not support the audio tag.
                  </audio>
                </div>
              )}

              {/* PDF */}
              {this.state.mediaTypes.browserDocument.includes(mediaType) && (
                <div className="d-flex align-items-center">
                  <iframe
                    style={{ width: "100%" }}
                    title="message-media"
                    src={
                      this.youtubeId(this.props.mediaUrl) !== undefined
                        ? `https://www.youtube.com/embed/${this.youtubeId(
                            this.props.mediaUrl
                          )}`
                        : this.props.mediaUrl
                    }
                    height="200rem"
                    frameBorder="0"
                    allow="fullscreen"
                    allowFullScreen
                  />
                  <FontAwesomeIcon
                    icon={faMagnifyingGlassPlus}
                    style={{
                      fontSize: "18px",
                      marginLeft: "10px",
                    }}
                  />
                </div>
              )}

              {/* Others */}
              {isNotImageOrVideo && (
                <div>
                  <p className="text-left" style={{color: "black"}}>
                    Documento:{" "}
                    {this.props.mediaUrl.substring(
                      this.props.mediaUrl.lastIndexOf("/") + 1
                    )}
                  </p>
                </div>
              )}
            </button>

            {/* Download button */}
            <a
              href={this.props.mediaUrl}
              target="_blank"
              rel="noreferrer"
              style={{ width: "fit-content" }}
            >
              <FontAwesomeIcon
                icon={faDownload}
                style={{
                  fontSize: "18px",
                  marginTop: "10px",
                }}
              />
            </a>
          </div>

          {/* Full screen media */}
          {this.props?.mediaUrl?.search(/http/i) !== -1 ? (
            <FsLightbox
              toggler={this.state.toggler}
              sources={[
                isNotImageOrVideo ? (
                  <iframe
                    title="message-url"
                    src={
                      this.youtubeId(this.props.mediaUrl) !== undefined
                        ? `https://www.youtube.com/embed/${this.youtubeId(
                            this.props.mediaUrl
                          )}`
                        : this.props.mediaUrl
                    }
                    width="1920px"
                    height="1080px"
                    style={{ border: "none" }}
                    allow="fullscreen"
                    allowFullScreen
                  />
                ) : (
                  this.props.mediaUrl
                ),
              ]}
              type={
                isNotImageOrVideo
                  ? null
                  : this.state.mediaTypes.image.includes(mediaType)
                  ? "image"
                  : "video"
              }
            />
          ) : null}
        </div>
      </>
    );
  }
}

export default MediaMessage;
