import { useState } from "react"

export default function MessageButton({ buttons, sendButton }) {
  const [disabled, setDisabled] = useState(false)

  const handleClick = (buttonTitle) => {
    sendButton(buttonTitle)
    setDisabled(prev => { return !prev })
  }

  return (
    <div style={{
      display: 'grid',
      gridTemplateColumns: 'repeat(2, minmax(0, 1fr))'
    }}>
      {buttons.map((button) => {
        return (
          <button
            style={{
              background: disabled ? 'gray' : 'linear-gradient(to bottom, #3d94f6 5%, #1e62d0 100%)',
              backgroundColor: '#3d94f6',
              borderRadius: '5px',
              display: 'inline-block',
              cursor: disabled ? '' : 'pointer',
              color: '#ffffff',
              fontSize: '15px',
              padding: '6px 22px',
              textDecoration: 'none',
            }}
            onClick={() => handleClick(button.title)}
            disabled={disabled}
          >{button.title}</button>
        )
      }
      )}
    </div>
  )
}